    'use client'
    import React, { useEffect, useMemo, useState } from "react";

    type Role = "admin" | "editor" | "viewer";

    interface User {
        id: number;
        name: string;
        email: string;
        role: Role;
        active: boolean;
    }

    const sampleData: User[] = [
        { id: 1, name: "Alice Johnson", email: "alice@example.com", role: "admin", active: true },
        { id: 2, name: "Bob Martin", email: "bob@example.com", role: "editor", active: true },
        { id: 3, name: "Carol Smith", email: "carol@example.com", role: "viewer", active: false },
    ];

    function uid(): number {
        return Math.floor(Math.random() * 1_000_000);
    }

    export default function AdminPanel(): JSX.Element {
        const [users, setUsers] = useState<User[]>(() => {
            try {
                const raw = localStorage.getItem("admin_users");
                return raw ? JSON.parse(raw) as User[] : sampleData;
            } catch {
                return sampleData;
            }
        });

        const [query, setQuery] = useState("");
        const [roleFilter, setRoleFilter] = useState<"all" | Role>("all");
        const [sortBy, setSortBy] = useState<"name" | "email" | "role">("name");

        const [isAdding, setIsAdding] = useState(false);
        const [editingId, setEditingId] = useState<number | null>(null);

        const [form, setForm] = useState<{ name: string; email: string; role: Role; active: boolean }>({
            name: "",
            email: "",
            role: "viewer",
            active: true,
        });

        useEffect(() => {
            localStorage.setItem("admin_users", JSON.stringify(users));
        }, [users]);

        useEffect(() => {
            if (editingId == null) {
                setForm({ name: "", email: "", role: "viewer", active: true });
            } else {
                const u = users.find((x) => x.id === editingId);
                if (u) setForm({ name: u.name, email: u.email, role: u.role, active: u.active });
            }
        }, [editingId, users]);

        const filtered = useMemo(() => {
            return users
                .filter((u) => (roleFilter === "all" ? true : u.role === roleFilter))
                .filter((u) => {
                    const q = query.trim().toLowerCase();
                    if (!q) return true;
                    return u.name.toLowerCase().includes(q) || u.email.toLowerCase().includes(q) || u.role.includes(q);
                })
                .sort((a, b) => {
                    if (sortBy === "name") return a.name.localeCompare(b.name);
                    if (sortBy === "email") return a.email.localeCompare(b.email);
                    return a.role.localeCompare(b.role);
                });
        }, [users, query, roleFilter, sortBy]);

        function startAdd() {
            setIsAdding(true);
            setEditingId(null);
            setForm({ name: "", email: "", role: "viewer", active: true });
        }

        function startEdit(id: number) {
            setEditingId(id);
            setIsAdding(false);
        }

        function cancelEdit() {
            setEditingId(null);
            setIsAdding(false);
        }

        function submitForm(e?: React.FormEvent) {
            e?.preventDefault();
            const trimmedName = form.name.trim();
            const trimmedEmail = form.email.trim();
            if (!trimmedName || !trimmedEmail) return;

            if (editingId != null) {
                setUsers((prev) =>
                    prev.map((u) => (u.id === editingId ? { ...u, name: trimmedName, email: trimmedEmail, role: form.role, active: form.active } : u))
                );
                setEditingId(null);
            } else {
                const newUser: User = {
                    id: uid(),
                    name: trimmedName,
                    email: trimmedEmail,
                    role: form.role,
                    active: form.active,
                };
                setUsers((prev) => [newUser, ...prev]);
                setIsAdding(false);
            }
            setForm({ name: "", email: "", role: "viewer", active: true });
        }

        function removeUser(id: number) {
            if (!confirm("Delete user? This action cannot be undone.")) return;
            setUsers((prev) => prev.filter((u) => u.id !== id));
        }

        function toggleActive(id: number) {
            setUsers((prev) => prev.map((u) => (u.id === id ? { ...u, active: !u.active } : u)));
        }

        return (
            <div style={styles.page}>
                <aside style={styles.sidebar}>
                    <h2 style={{ margin: 0 }}>Admin Panel</h2>
                    <nav style={{ marginTop: 16 }}>
                        <button style={styles.sideButton} onClick={() => { setRoleFilter("all"); setQuery(""); }}>
                            Dashboard
                        </button>
                        <button style={styles.sideButton} onClick={() => { setRoleFilter("all"); }}>
                            Users
                        </button>
                        <button
                            style={styles.sideButton}
                            onClick={() => {
                                startAdd();
                            }}
                        >
                            + Add user
                        </button>
                    </nav>
                    <div style={{ marginTop: "auto", fontSize: 12, opacity: 0.8 }}>Local demo • changes saved to localStorage</div>
                </aside>

                <main style={styles.main}>
                    <header style={styles.header}>
                        <div>
                            <input
                                aria-label="Search"
                                placeholder="Search name, email, role..."
                                value={query}
                                onChange={(e) => setQuery(e.target.value)}
                                style={styles.search}
                            />
                            <select value={roleFilter} onChange={(e) => setRoleFilter(e.target.value as any)} style={styles.select}>
                                <option value="all">All roles</option>
                                <option value="admin">Admin</option>
                                <option value="editor">Editor</option>
                                <option value="viewer">Viewer</option>
                            </select>
                            <select value={sortBy} onChange={(e) => setSortBy(e.target.value as any)} style={styles.select}>
                                <option value="name">Sort: Name</option>
                                <option value="email">Sort: Email</option>
                                <option value="role">Sort: Role</option>
                            </select>
                        </div>
                        <div>
                            <button style={styles.primary} onClick={startAdd}>
                                New user
                            </button>
                        </div>
                    </header>

                    <section style={{ padding: 12 }}>
                        {(isAdding || editingId != null) && (
                            <form onSubmit={submitForm} style={styles.card}>
                                <h3 style={{ marginTop: 0 }}>{editingId != null ? "Edit user" : "Add new user"}</h3>
                                <label style={styles.field}>
                                    Name
                                    <input value={form.name} onChange={(e) => setForm((s) => ({ ...s, name: e.target.value }))} />
                                </label>
                                <label style={styles.field}>
                                    Email
                                    <input value={form.email} onChange={(e) => setForm((s) => ({ ...s, email: e.target.value }))} />
                                </label>
                                <label style={styles.fieldInline}>
                                    Role
                                    <select value={form.role} onChange={(e) => setForm((s) => ({ ...s, role: e.target.value as Role }))}>
                                        <option value="admin">Admin</option>
                                        <option value="editor">Editor</option>
                                        <option value="viewer">Viewer</option>
                                    </select>
                                    <label style={{ marginLeft: 12, display: "flex", alignItems: "center", gap: 8 }}>
                                        <input type="checkbox" checked={form.active} onChange={(e) => setForm((s) => ({ ...s, active: e.target.checked }))} />
                                        Active
                                    </label>
                                </label>
                                <div style={{ display: "flex", gap: 8, marginTop: 12 }}>
                                    <button type="submit" style={styles.primary}>
                                        {editingId != null ? "Save" : "Create"}
                                    </button>
                                    <button type="button" onClick={cancelEdit} style={styles.secondary}>
                                        Cancel
                                    </button>
                                </div>
                            </form>
                        )}

                        <div style={styles.tableCard}>
                            <table style={styles.table}>
                                <thead>
                                    <tr>
                                        <th style={styles.th}>#</th>
                                        <th style={styles.th}>Name</th>
                                        <th style={styles.th}>Email</th>
                                        <th style={styles.th}>Role</th>
                                        <th style={styles.th}>Active</th>
                                        <th style={styles.th}>Actions</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {filtered.length === 0 && (
                                        <tr>
                                            <td colSpan={6} style={{ textAlign: "center", padding: 16, color: "#666" }}>
                                                No users found
                                            </td>
                                        </tr>
                                    )}
                                    {filtered.map((u, i) => (
                                        <tr key={u.id} style={i % 2 ? { background: "#fafafa" } : undefined}>
                                            <td style={styles.td}>{u.id}</td>
                                            <td style={styles.td}>{u.name}</td>
                                            <td style={styles.td}>{u.email}</td>
                                            <td style={styles.td}>{u.role}</td>
                                            <td style={styles.td}>
                                                <button onClick={() => toggleActive(u.id)} style={styles.toggle(u.active)}>
                                                    {u.active ? "Active" : "Inactive"}
                                                </button>
                                            </td>
                                            <td style={styles.td}>
                                                <button onClick={() => startEdit(u.id)} style={styles.small}>
                                                    Edit
                                                </button>
                                                <button onClick={() => removeUser(u.id)} style={{ ...styles.small, marginLeft: 8, background: "#ffecec", color: "#a00" }}>
                                                    Delete
                                                </button>
                                            </td>
                                        </tr>
                                    ))}
                                </tbody>
                            </table>
                        </div>
                    </section>
                </main>
            </div>
        );
    }

    const styles: { [k: string]: any } = {
        page: {
            display: "flex",
            height: "100vh",
            fontFamily: "Inter, ui-sans-serif, system-ui, -apple-system, 'Segoe UI', Roboto, 'Helvetica Neue', Arial",
            color: "#111",
        },
        sidebar: {
            width: 220,
            borderRight: "1px solid #eee",
            padding: 16,
            display: "flex",
            flexDirection: "column",
            gap: 12,
        },
        sideButton: {
            display: "block",
            width: "100%",
            textAlign: "left",
            background: "transparent",
            border: "none",
            padding: "8px 6px",
            cursor: "pointer",
            borderRadius: 6,
        },
        main: {
            flex: 1,
            display: "flex",
            flexDirection: "column",
        },
        header: {
            padding: 12,
            borderBottom: "1px solid #eee",
            display: "flex",
            justifyContent: "space-between",
            alignItems: "center",
            gap: 12,
        },
        search: {
            padding: 8,
            borderRadius: 8,
            border: "1px solid #ddd",
            width: 260,
            marginRight: 8,
        },
        select: {
            padding: 8,
            borderRadius: 8,
            border: "1px solid #ddd",
            marginLeft: 8,
        },
        primary: {
            background: "#2563eb",
            color: "#fff",
            border: "none",
            padding: "8px 12px",
            borderRadius: 8,
            cursor: "pointer",
        },
        secondary: {
            background: "#f3f4f6",
            border: "1px solid #e5e7eb",
            padding: "8px 12px",
            borderRadius: 8,
            cursor: "pointer",
        },
        card: {
            border: "1px solid #eee",
            padding: 12,
            borderRadius: 8,
            marginBottom: 12,
            maxWidth: 720,
        },
        field: {
            display: "flex",
            flexDirection: "column",
            gap: 6,
            marginTop: 8,
        },
        fieldInline: {
            display: "flex",
            alignItems: "center",
            gap: 12,
            marginTop: 8,
        },
        tableCard: {
            border: "1px solid #eee",
            borderRadius: 8,
            overflow: "hidden",
        },
        table: {
            width: "100%",
            borderCollapse: "collapse" as const,
        },
        th: {
            textAlign: "left" as const,
            padding: "12px 16px",
            background: "#fafafa",
            borderBottom: "1px solid #eee",
            fontSize: 13,
            color: "#333",
        },
        td: {
            padding: "10px 16px",
            fontSize: 14,
            verticalAlign: "middle" as const,
        },
        small: {
            background: "#f3f4f6",
            border: "none",
            padding: "6px 8px",
            borderRadius: 6,
            cursor: "pointer",
        },
        toggle: (active: boolean) => ({
            background: active ? "#dcfce7" : "#fff1f0",
            border: "1px solid " + (active ? "#bbf7d0" : "#fecaca"),
            padding: "6px 8px",
            borderRadius: 6,
            cursor: "pointer",
        }),
    };